const state = {
    currentUser: localStorage.getItem('currentUser') || null,
    fullName: null || localStorage.getItem('fullName')
}

const mutations = {
    SET_USER(state, newValue){
        state.currentUser = JSON.stringify(newValue)
        saveState('currentUser', newValue)
    },
    DELETE_USER(state){
        state.currentUser = null
        state.fullName = null
        deleteState('currentUser')
    },
    SET_FULL_NAME(state, payload){
        state.fullName = payload
        localStorage.setItem('fullName', payload)
    }
}

const actions = {
    login({ commit }, variables ){
        commit('SET_USER', variables)
        commit('SET_FULL_NAME', variables.fullName)
    },
    logout({ commit }){
        commit('DELETE_USER')
    }
}

const getters = {
    loggedIn(state){
        return !!state.currentUser
    },
    getPermissions(state){
        if(state.currentUser !== null){
            return JSON.parse(state.currentUser).permissions
        }
        return []
    },
    getFullName(state){
        return state.fullName
    }
}

const saveState = (key, value) =>{
    localStorage.setItem(key, JSON.stringify(value))
}

const deleteState = ( key ) => {
    localStorage.removeItem(key)
    localStorage.clear()
}

export {
    state,
    mutations,
    actions,
    getters
}