import moment from 'moment'
import { getTokenCurrentUser } from '@/utils/auth'
import { encode_bs64 } from '@/utils/general'

const formatDataNotification = ( data ) =>{
    const encodeId = encode_bs64(`NotificationNode:${data.id}`)
    return {
        id: encodeId,
        title: data.title,
        subtitle: data.subtitle,
        text: data.text,
        created: moment().format('LLL'),
        recipient: data.recipient,
        products: data.products,
        read: false
    }
}

export const state = {
    socket: null,
    allNotifications: []
}

export const mutations = {
    SET_SOCKET(state, socket){
        state.socket = socket
    },
    SET_NOTIFICATIONS(state, data){
        const tempNotifications = []
        for (let item of data.data){
            const index = state.allNotifications.findIndex(notify => notify.id === item.id)
            if (index === -1){
                tempNotifications.push(item)
            }
        }
        if (data.type === 'websocket'){
            state.allNotifications.unshift(...tempNotifications)
        }else{
            state.allNotifications.push(...tempNotifications)
        }
    },
    READ_NOTIFICATION(state, notification_id){
        const index = state.allNotifications.findIndex(notify => notify.id === notification_id)
        const notification = state.allNotifications[index]
        notification.read = true
        state.allNotifications[index] = notification
    }
}

export const actions = {
    connectSocket({ commit }){
        const token = getTokenCurrentUser()
        const socketUrl = `${process.env.VUE_APP_SOCKET_URL}/ws/notifications?authorization=Bearer ${token}`
        const socket = new WebSocket(socketUrl)
        socket.onmessage = ( info ) => {
            const { data } = info
            const formatedData = formatDataNotification(JSON.parse(data))
            commit('SET_NOTIFICATIONS', {data: [formatedData], type: 'websocket'})
        }
        commit('SET_SOCKET', socket)
    },
    
}
export const getters = {
    hasSocket(state){
        return !!state.socket
    },
    listNotifications(state){
        return state.allNotifications
    },
    getSocket(state){
        return state.socket
    },
    countNotifications(state){
        return state.allNotifications.filter(notify => notify.read === false).length
    }

}